/* src/components/PrivacyPolicy.css */
/* ===================== IMPORT THEME TOKENS FROM LOGIN.CSS ===================== */
:root {
  /* Base */
  --bg-primary: #ffffff;
  --bg-secondary: #f7f8fb;
  --bg-tertiary: #eef1f6;

  /* Text */
  --text-primary: #101226;
  --text-secondary: #5a6080;

  /* Brand */
  --accent-primary: #5b7cfa;
  --accent-secondary: #9b5ef7;
  --accent-tertiary: #ff6b6b;

  /* UI */
  --border-color: #e5e9f2;
  --glass-bg: rgba(255, 255, 255, 0.85);
  --glass-border: rgba(255, 255, 255, 0.25);

  /* Shadows */
  --shadow-light: 0 6px 16px rgba(16, 18, 38, 0.06);
  --shadow-heavy: 0 18px 40px rgba(16, 18, 38, 0.14);

  /* Gradients */
  --gradient-light: linear-gradient(135deg, #f9fbff 0%, #eef2f9 45%, #e5eaf6 100%);
  --gradient-accent: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
  --gradient-dark: linear-gradient(135deg, #4966f7, #7a54fa);

  /* Legacy variables for backward compatibility */
  --dark-highlight: #8b5cf6;
  --dark-highlight-hover: #7c3aed;
  --light-highlight: #7c3aed;
  --light-highlight-hover: #6d28d9;
  --dark-text-primary: #eef1ff;
  --dark-text-secondary: #9aa2c7;
  --light-text-primary: #101226;
  --light-text-secondary: #5a6080;
}

.dark {
  --bg-primary: #0e1024;
  --bg-secondary: #121632;
  --bg-tertiary: #0a0f27;

  --text-primary: #eef1ff;
  --text-secondary: #9aa2c7;

  --border-color: rgba(255, 255, 255, 0.08);
  --glass-bg: rgba(18, 22, 50, 0.78);
  --glass-border: rgba(255, 255, 255, 0.08);

  --shadow-light: 0 10px 24px rgba(0, 0, 0, 0.28);
  --shadow-heavy: 0 24px 60px rgba(0, 0, 0, 0.4);

  --gradient-light: linear-gradient(135deg, #0e1024 0%, #121632 50%, #0a0f27 100%);
  --gradient-accent: linear-gradient(135deg, #5b7cfa, #9b5ef7);
  --gradient-dark: linear-gradient(135deg, #5b7cfa, #9b5ef7);
}

/* ===================== BACKGROUND ELEMENTS ===================== */
.privacy-policy-container {
  position: relative;
  min-height: 100vh;
  background: var(--gradient-light);
  color: var(--text-primary);
  overflow-x: hidden;
  font-family: "Inter", "Segoe UI", system-ui, -apple-system, Roboto, Arial, sans-serif;
  transition: background 0.4s ease, color 0.2s ease;
  padding: 2rem;
  padding-top: 8rem;
}

.privacy-policy-container::before {
  content: "";
  position: fixed;
  inset: 0;
  background:
    radial-gradient(circle at 12% 55%, rgba(91,124,250,0.10) 0%, transparent 52%),
    radial-gradient(circle at 88% 28%, rgba(155,94,247,0.10) 0%, transparent 48%),
    radial-gradient(circle at 50% 92%, rgba(255,107,107,0.06) 0%, transparent 50%);
  z-index: -1;
  animation: bgFloat 20s ease-in-out infinite;
}

@keyframes bgFloat {
  0%, 100% { transform: translate(0,0) scale(1); opacity: 1; }
  40%      { transform: translate(28px,-24px) scale(1.05); opacity: 0.95; }
  70%      { transform: translate(-18px,18px) scale(0.98); opacity: 0.9;  }
}

.cyber-grid {
  position: fixed;
  inset: 0;
  background-image:
    linear-gradient(rgba(91,124,250,0.06) 1px, transparent 1px),
    linear-gradient(90deg, rgba(91,124,250,0.06) 1px, transparent 1px);
  background-size: 44px 44px;
  z-index: 0;
  animation: gridShift 40s linear infinite;
}

@keyframes gridShift {
  from { transform: translate(0,0); opacity: 0.35; }
  to   { transform: translate(44px,44px); opacity: 0.65; }
}

.geometric-bg { position: fixed; inset: 0; z-index: 0; opacity: 0.45; pointer-events: none; }
.shape {
  position: absolute;
  background: var(--gradient-accent);
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  animation: morph 16s ease-in-out infinite alternate;
  opacity: 0.12; filter: blur(18px);
}
.shape:nth-child(1){ width:420px; height:420px; top:-220px; right:-160px; }
.shape:nth-child(2){ width:320px; height:320px; bottom:-120px; left:-120px; background: var(--gradient-dark); animation-delay: 2.2s; }
.shape:nth-child(3){ width:220px; height:220px; top:48%; right:8%; animation-delay: 4.3s; }

@keyframes morph {
  0%   { border-radius: 28% 72% 68% 32%/32% 28% 72% 68%; transform: rotate(0deg); }
  100% { border-radius: 70% 30% 30% 70%/68% 72% 28% 32%; transform: rotate(180deg); }
}

.floating-particles { position: fixed; inset: 0; z-index: 0; pointer-events: none; }
.particle {
  position: absolute; width: 4px; height: 4px; border-radius: 50%;
  background: var(--gradient-accent); opacity: 0.28; filter: blur(1px);
  animation: floatParticle 8s ease-in-out infinite;
}
.particle:nth-child(odd){ background: var(--gradient-dark); animation-delay: -2s; }
@keyframes floatParticle {
  0%,100% { transform: translate(0,0) scale(1); opacity: 0.22; }
  25%     { transform: translate(90px,-50px) scale(1.15); opacity: 0.4; }
  55%     { transform: translate(40px,-95px) scale(0.9);  opacity: 0.3; }
  80%     { transform: translate(-45px,-70px) scale(1.1); opacity: 0.35; }
}

.privacy-policy-container {
  min-height: 100vh;
  position: relative;
  padding: 2rem;
  padding-top: 8rem;
}

.privacy-content {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.privacy-header {
  text-align: center;
  margin-bottom: 4rem;
  animation: fadeInUp 0.8s ease-out;
}

.privacy-header h1 {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 4rem;
  font-weight: 800;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, var(--dark-highlight), var(--dark-highlight-hover));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: -2px;
}

body:not(.dark) .privacy-header h1 {
  background: linear-gradient(135deg, var(--light-highlight), var(--light-highlight-hover));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.last-updated {
  font-size: 1.1rem;
  opacity: 0.8;
  font-weight: 500;
}

.privacy-sections {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

.privacy-section {
  padding: 3rem;
  border-radius: 24px;
  backdrop-filter: blur(20px);
  animation: slideInUp 0.6s ease-out;
  transition: all 0.3s ease;
}

body.dark .privacy-section {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
}

body:not(.dark) .privacy-section {
  background: rgba(255, 255, 255, 0.7);
  border: 1px solid rgba(0, 0, 0, 0.06);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.08);
}

.privacy-section:hover {
  transform: translateY(-5px);
}

body.dark .privacy-section:hover {
  border-color: var(--dark-highlight);
  box-shadow: 0 12px 40px rgba(139, 92, 246, 0.2);
}

body:not(.dark) .privacy-section:hover {
  border-color: var(--light-highlight);
  box-shadow: 0 12px 40px rgba(139, 92, 246, 0.15);
}

.privacy-section h2 {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}

body.dark .privacy-section h2 {
  color: var(--dark-text-primary);
}

body:not(.dark) .privacy-section h2 {
  color: var(--light-text-primary);
}

.privacy-section p {
  font-size: 1.1rem;
  line-height: 1.7;
  margin-bottom: 1.5rem;
}

body.dark .privacy-section p {
  color: var(--dark-text-secondary);
}

body:not(.dark) .privacy-section p {
  color: var(--light-text-secondary);
}

.privacy-section ul {
  list-style: none;
  padding: 0;
  margin: 1.5rem 0;
}

.privacy-section li {
  padding: 0.75rem 0;
  padding-left: 2rem;
  position: relative;
  font-size: 1.1rem;
}

.privacy-section li::before {
  content: '';
  position: absolute;
  left: 0;
  font-weight: bold;
  color: var(--dark-highlight);
}

body:not(.dark) .privacy-section li::before {
  color: var(--light-highlight);
}

/* Info Categories */
.info-categories {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.info-category {
  padding: 2rem;
  border-radius: 16px;
  transition: all 0.3s ease;
}

body.dark .info-category {
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

body:not(.dark) .info-category {
  background: rgba(255, 255, 255, 0.5);
  border: 1px solid rgba(0, 0, 0, 0.04);
}

.info-category h3 {
  font-size: 1.3rem;
  margin-bottom: 1rem;
  color: var(--dark-highlight);
}

body:not(.dark) .info-category h3 {
  color: var(--light-highlight);
}

/* Usage Grid */
.usage-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.usage-card {
  text-align: center;
  padding: 2rem 1.5rem;
  border-radius: 16px;
  transition: all 0.3s ease;
}

body.dark .usage-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
}

body:not(.dark) .usage-card {
  background: rgba(255, 255, 255, 0.6);
  border: 1px solid rgba(0, 0, 0, 0.06);
}

.usage-card:hover {
  transform: translateY(-8px);
}

body.dark .usage-card:hover {
  border-color: var(--dark-highlight);
  box-shadow: 0 8px 32px rgba(139, 92, 246, 0.2);
}

body:not(.dark) .usage-card:hover {
  border-color: var(--light-highlight);
  box-shadow: 0 8px 32px rgba(139, 92, 246, 0.15);
}

.usage-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
  display: block;
}

.usage-card h4 {
  font-size: 1.2rem;
  margin-bottom: 1rem;
  font-weight: 600;
}

.usage-card p {
  font-size: 0.95rem;
  opacity: 0.9;
}

/* Security Features */
.security-features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin-top: 1.5rem;
}

.security-feature {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  border-radius: 12px;
  font-weight: 500;
}

body.dark .security-feature {
  background: rgba(255, 255, 255, 0.03);
}

body:not(.dark) .security-feature {
  background: rgba(255, 255, 255, 0.4);
}

.security-icon {
  font-size: 1.5rem;
}

/* Rights Grid */
.rights-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  margin-top: 2rem;
}

.right-card {
  padding: 1.5rem;
  border-radius: 12px;
  text-align: center;
  transition: all 0.3s ease;
}

body.dark .right-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
}

body:not(.dark) .right-card {
  background: rgba(255, 255, 255, 0.5);
  border: 1px solid rgba(0, 0, 0, 0.06);
}

.right-card:hover {
  transform: translateY(-5px);
}

.right-card h4 {
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
  color: var(--dark-highlight);
  font-weight: 600;
}

body:not(.dark) .right-card h4 {
  color: var(--light-highlight);
}

.right-card p {
  font-size: 0.9rem;
  margin: 0;
  opacity: 0.9;
}

/* Contact Info */
.contact-info {
  margin-top: 1.5rem;
  padding: 1.5rem;
  border-radius: 12px;
}

body.dark .contact-info {
  background: rgba(255, 255, 255, 0.03);
}

body:not(.dark) .contact-info {
  background: rgba(255, 255, 255, 0.4);
}

.contact-info p {
  margin: 0.5rem 0;
  font-size: 1.1rem;
}

/* Privacy Footer */
.privacy-footer {
  text-align: center;
  margin-top: 4rem;
  padding: 3rem;
  border-radius: 20px;
  backdrop-filter: blur(20px);
}

body.dark .privacy-footer {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
}

body:not(.dark) .privacy-footer {
  background: rgba(255, 255, 255, 0.7);
  border: 1px solid rgba(0, 0, 0, 0.06);
}

.privacy-footer p {
  font-size: 1.1rem;
  font-weight: 500;
  margin: 0;
}

/* Premium Navbar */
.premium-navbar {
  position: fixed;
  top: 2rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 90%;
  max-width: 1400px;
  padding: 1rem 2rem;
  border-radius: 20px;
  backdrop-filter: blur(25px);
  z-index: 1000;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

body.dark .premium-navbar {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.12);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

body:not(.dark) .premium-navbar {
  background: rgba(255, 255, 255, 0.95);
  border: 1px solid rgba(0, 0, 0, 0.08);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.12);
}

.nav-brand {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.nav-logo {
  width: 40px;
  height: 40px;
  border-radius: 10px;
  object-fit: contain;
}

.nav-brand-text {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 1.5rem;
  font-weight: 700;
  background: linear-gradient(135deg, var(--dark-highlight), var(--dark-highlight-hover));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

body:not(.dark) .nav-brand-text {
  background: linear-gradient(135deg, var(--light-highlight), var(--light-highlight-hover));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.nav-link {
  padding: 0.75rem 1.5rem;
  text-decoration: none;
  border-radius: 12px;
  font-weight: 600;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

body.dark .nav-link {
  color: var(--dark-text-secondary);
}

body:not(.dark) .nav-link {
  color: var(--light-text-secondary);
}

.nav-link::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transition: left 0.6s ease;
}

.nav-link:hover::before {
  left: 100%;
}

.nav-link:hover,
.nav-link.active {
  transform: translateY(-2px);
}

body.dark .nav-link:hover,
body.dark .nav-link.active {
  background: rgba(139, 92, 246, 0.15);
  color: var(--dark-highlight);
  box-shadow: 0 4px 20px rgba(139, 92, 246, 0.25);
}

body:not(.dark) .nav-link:hover,
body:not(.dark) .nav-link.active {
  background: rgba(139, 92, 246, 0.1);
  color: var(--light-highlight);
  box-shadow: 0 4px 20px rgba(139, 92, 246, 0.15);
}

.nav-login-btn {
  padding: 0.75rem 2rem;
  border-radius: 12px;
  border: none;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  background: linear-gradient(135deg, var(--dark-highlight), var(--dark-highlight-hover));
  color: white;
  box-shadow: 0 4px 20px rgba(139, 92, 246, 0.3);
}

.nav-login-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 30px rgba(139, 92, 246, 0.4);
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .privacy-policy-container {
    padding: 1rem;
    padding-top: 7rem;
  }

  .privacy-header h1 {
    font-size: 2.5rem;
  }

  .privacy-section {
    padding: 2rem 1.5rem;
  }

  .premium-navbar {
    flex-direction: column;
    gap: 1rem;
    padding: 1rem;
  }

  .nav-links {
    gap: 1rem;
  }

  .nav-link {
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
  }

  .info-categories,
  .usage-grid,
  .rights-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .privacy-header h1 {
    font-size: 2rem;
  }

  .privacy-section h2 {
    font-size: 1.5rem;
  }

  .nav-brand-text {
    font-size: 1.2rem;
  }
}