/* src/components/About.css - WITH FIXED NAVBAR STYLES */

/* ===================== IMPORT THEME TOKENS FROM LOGIN.CSS ===================== */
:root {
  /* Base */
  --bg-primary: #ffffff;
  --bg-secondary: #f7f8fb;
  --bg-tertiary: #eef1f6;

  /* Text */
  --text-primary: #101226;
  --text-secondary: #5a6080;

  /* Brand */
  --accent-primary: #5b7cfa;
  --accent-secondary: #9b5ef7;
  --accent-tertiary: #ff6b6b;

  /* UI */
  --border-color: #e5e9f2;
  --glass-bg: rgba(255, 255, 255, 0.85);
  --glass-border: rgba(255, 255, 255, 0.25);

  /* Shadows */
  --shadow-light: 0 6px 16px rgba(16, 18, 38, 0.06);
  --shadow-heavy: 0 18px 40px rgba(16, 18, 38, 0.14);

  /* Gradients */
  --gradient-light: linear-gradient(135deg, #f9fbff 0%, #eef2f9 45%, #e5eaf6 100%);
  --gradient-accent: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
  --gradient-dark: linear-gradient(135deg, #4966f7, #7a54fa);

  /* Legacy variables for backward compatibility */
  --dark-highlight: #8b5cf6;
  --dark-highlight-hover: #7c3aed;
  --light-highlight: #7c3aed;
  --light-highlight-hover: #6d28d9;
  --dark-text-primary: #eef1ff;
  --dark-text-secondary: #9aa2c7;
  --light-text-primary: #101226;
  --light-text-secondary: #5a6080;

  /* Focus */
  --focus-ring: 0 0 0 3px rgba(91, 124, 250, 0.35);
}

.dark {
  --bg-primary: #0e1024;
  --bg-secondary: #121632;
  --bg-tertiary: #0a0f27;

  --text-primary: #eef1ff;
  --text-secondary: #9aa2c7;

  --border-color: rgba(255, 255, 255, 0.08);
  --glass-bg: rgba(18, 22, 50, 0.78);
  --glass-border: rgba(255, 255, 255, 0.08);

  --shadow-light: 0 10px 24px rgba(0, 0, 0, 0.28);
  --shadow-heavy: 0 24px 60px rgba(0, 0, 0, 0.4);

  --gradient-light: linear-gradient(135deg, #0e1024 0%, #121632 50%, #0a0f27 100%);
  --gradient-accent: linear-gradient(135deg, #5b7cfa, #9b5ef7);
  --gradient-dark: linear-gradient(135deg, #5b7cfa, #9b5ef7);

  --focus-ring: 0 0 0 3px rgba(155, 94, 247, 0.35);
}

/* ===================== BACKGROUND ELEMENTS ===================== */
.about-container {
  position: relative;
  min-height: 100vh;
  background: var(--gradient-light);
  color: var(--text-primary);
  overflow-x: hidden;
  font-family: "Inter", "Segoe UI", system-ui, -apple-system, Roboto, Arial, sans-serif;
  transition: background 0.4s ease, color 0.2s ease;
}

.about-container::before {
  content: "";
  position: fixed;
  inset: 0;
  background:
    radial-gradient(circle at 12% 55%, rgba(91,124,250,0.10) 0%, transparent 52%),
    radial-gradient(circle at 88% 28%, rgba(155,94,247,0.10) 0%, transparent 48%),
    radial-gradient(circle at 50% 92%, rgba(255,107,107,0.06) 0%, transparent 50%);
  z-index: -1;
  animation: bgFloat 20s ease-in-out infinite;
}

@keyframes bgFloat {
  0%, 100% { transform: translate(0,0) scale(1); opacity: 1; }
  40%      { transform: translate(28px,-24px) scale(1.05); opacity: 0.95; }
  70%      { transform: translate(-18px,18px) scale(0.98); opacity: 0.9;  }
}

.cyber-grid {
  position: fixed;
  inset: 0;
  background-image:
    linear-gradient(rgba(91,124,250,0.06) 1px, transparent 1px),
    linear-gradient(90deg, rgba(91,124,250,0.06) 1px, transparent 1px);
  background-size: 44px 44px;
  z-index: 0;
  animation: gridShift 40s linear infinite;
}

@keyframes gridShift {
  from { transform: translate(0,0); opacity: 0.35; }
  to   { transform: translate(44px,44px); opacity: 0.65; }
}

.geometric-bg { position: fixed; inset: 0; z-index: 0; opacity: 0.45; pointer-events: none; }
.shape {
  position: absolute;
  background: var(--gradient-accent);
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  animation: morph 16s ease-in-out infinite alternate;
  opacity: 0.12; filter: blur(18px);
}
.shape:nth-child(1){ width:420px; height:420px; top:-220px; right:-160px; }
.shape:nth-child(2){ width:320px; height:320px; bottom:-120px; left:-120px; background: var(--gradient-dark); animation-delay: 2.2s; }
.shape:nth-child(3){ width:220px; height:220px; top:48%; right:8%; animation-delay: 4.3s; }

@keyframes morph {
  0%   { border-radius: 28% 72% 68% 32%/32% 28% 72% 68%; transform: rotate(0deg); }
  100% { border-radius: 70% 30% 30% 70%/68% 72% 28% 32%; transform: rotate(180deg); }
}

.floating-particles { position: fixed; inset: 0; z-index: 0; pointer-events: none; }
.particle {
  position: absolute; width: 4px; height: 4px; border-radius: 50%;
  background: var(--gradient-accent); opacity: 0.28; filter: blur(1px);
  animation: floatParticle 8s ease-in-out infinite;
}
.particle:nth-child(odd){ background: var(--gradient-dark); animation-delay: -2s; }
@keyframes floatParticle {
  0%,100% { transform: translate(0,0) scale(1); opacity: 0.22; }
  25%     { transform: translate(90px,-50px) scale(1.15); opacity: 0.4; }
  55%     { transform: translate(40px,-95px) scale(0.9);  opacity: 0.3; }
  80%     { transform: translate(-45px,-70px) scale(1.1); opacity: 0.35; }
}

/* ===================== NAVBAR - MATCHING LOGIN.CSS ===================== */
.premium-navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: var(--glass-bg);
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  border-bottom: 1px solid var(--border-color);
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 1000;
  box-shadow: var(--shadow-light);
}

.nav-left-section {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  flex-shrink: 0;
}

.nav-brand {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-shrink: 0;
}

.nav-logo {
  width: 46px;
  height: 46px;
  border-radius: 12px;
  object-fit: cover;
  background: var(--gradient-accent);
  padding: 2px;
  flex-shrink: 0;
}

.nav-brand-text {
  font-size: 1.6rem;
  font-weight: 800;
  background: var(--gradient-accent);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  flex-shrink: 0;
  white-space: nowrap;
}

.theme-toggle-btn {
  width: 44px;
  height: 44px;
  border-radius: 10px;
  background: var(--glass-bg);
  border: 1px solid var(--border-color);
  color: var(--text-primary);
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  flex-shrink: 0;
  box-shadow: var(--shadow-light);
}

.theme-toggle-btn:hover {
  transform: scale(1.1);
  border-color: var(--accent-primary);
  background: var(--bg-secondary);
  box-shadow: 0 4px 12px rgba(91, 124, 250, 0.3);
}

.theme-toggle-btn:active {
  transform: scale(0.95);
}

.nav-links {
  display: flex;
  gap: 2.4rem;
  align-items: center;
  flex-shrink: 0;
}

.nav-link {
  color: var(--text-primary);
  text-decoration: none;
  font-weight: 600;
  position: relative;
  transition: color 0.25s ease;
  white-space: nowrap;
}

.nav-link:hover,
.nav-link.active {
  color: var(--accent-primary);
}

.nav-link.active::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -6px;
  height: 2px;
  width: 100%;
  background: var(--gradient-accent);
  border-radius: 2px;
  animation: slideIn 0.3s ease;
}

@keyframes slideIn {
  from { width: 0; }
  to { width: 100%; }
}

.nav-controls {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-shrink: 0;
}

.nav-login-btn {
  background: var(--gradient-accent);
  color: #fff;
  border: none;
  padding: 0.8rem 1.6rem;
  border-radius: 12px;
  font-weight: 700;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: transform 0.25s ease, box-shadow 0.25s ease;
  box-shadow: var(--shadow-light);
  white-space: nowrap;
}

.nav-login-btn::before {
  content: "";
  position: absolute;
  inset: 0;
  left: -100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.28), transparent);
  transition: left 0.55s ease;
}

.nav-login-btn:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-heavy);
}

.nav-login-btn:hover::before {
  left: 100%;
}

/* ===================== MAIN CONTENT ===================== */
.about-content {
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
  padding-top: 8rem;
}

/* Hero Section */
.about-hero {
  text-align: center;
  padding: 4rem 0;
  margin-bottom: 4rem;
  animation: fadeInUp 0.8s ease-out;
}

.about-hero h1 {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 4rem;
  font-weight: 800;
  margin-bottom: 1.5rem;
  background: var(--gradient-accent);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: -2px;
}

.hero-subtitle {
  font-size: 1.4rem;
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto 3rem;
  opacity: 0.9;
  font-weight: 500;
  color: var(--text-secondary);
}

.hero-stats {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
  max-width: 800px;
  margin: 0 auto;
}

.stat {
  padding: 2rem 1rem;
  border-radius: 20px;
  transition: all 0.3s ease;
  background: var(--glass-bg);
  border: 1px solid var(--border-color);
  box-shadow: var(--shadow-light);
}

.stat:hover {
  transform: translateY(-5px);
  border-color: var(--accent-primary);
  box-shadow: var(--shadow-heavy);
}

.stat-number {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
  background: var(--gradient-accent);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.stat-label {
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  color: var(--text-secondary);
}

/* Section Container */
.section-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

/* Mission Section */
.mission-section {
  padding: 6rem 0;
  margin-bottom: 4rem;
}

.mission-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
}

.mission-content h2 {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 2rem;
  color: var(--text-primary);
}

.mission-content p {
  font-size: 1.2rem;
  line-height: 1.7;
  margin-bottom: 1.5rem;
  color: var(--text-secondary);
}

.mission-content strong {
  background: var(--gradient-accent);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 700;
}

.mission-visual {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.floating-card {
  padding: 2rem;
  border-radius: 20px;
  text-align: center;
  transition: all 0.3s ease;
  animation: float 6s ease-in-out infinite;
  background: var(--glass-bg);
  border: 1px solid var(--border-color);
  box-shadow: var(--shadow-light);
}

.floating-card:nth-child(1) {
  animation-delay: 0s;
}

.floating-card:nth-child(2) {
  animation-delay: 2s;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

.card-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
  display: block;
}

.floating-card h4 {
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: var(--text-primary);
}

.floating-card p {
  font-size: 1rem;
  opacity: 0.9;
  margin: 0;
  color: var(--text-secondary);
}

/* Story Section */
.story-section {
  padding: 6rem 0;
  margin-bottom: 4rem;
}

.story-section h2 {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 2.5rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 4rem;
  color: var(--text-primary);
}

.story-timeline {
  position: relative;
  max-width: 800px;
  margin: 0 auto;
}

.story-timeline::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 2px;
  background: linear-gradient(to bottom, var(--accent-primary), transparent);
  transform: translateX(-50%);
}

.timeline-item {
  position: relative;
  margin-bottom: 4rem;
  display: flex;
  align-items: center;
  opacity: 0;
  animation: fadeInUp 0.8s ease-out forwards;
}

.timeline-item:nth-child(1) { animation-delay: 0.1s; }
.timeline-item:nth-child(2) { animation-delay: 0.3s; }
.timeline-item:nth-child(3) { animation-delay: 0.5s; }
.timeline-item:nth-child(4) { animation-delay: 0.7s; }

.timeline-item:nth-child(odd) {
  flex-direction: row-reverse;
  text-align: right;
}

.timeline-year {
  flex: 0 0 120px;
  text-align: center;
  font-family: 'Space Grotesk', sans-serif;
  font-size: 1.5rem;
  font-weight: 700;
  padding: 1rem;
  border-radius: 12px;
  background: var(--gradient-accent);
  color: white;
  box-shadow: 0 4px 20px rgba(139, 92, 246, 0.3);
}

.timeline-content {
  flex: 1;
  padding: 2rem;
  margin: 0 2rem;
  border-radius: 16px;
  transition: all 0.3s ease;
  background: var(--glass-bg);
  border: 1px solid var(--border-color);
  box-shadow: var(--shadow-light);
}

.timeline-content:hover {
  transform: translateY(-5px);
  border-color: var(--accent-primary);
  box-shadow: var(--shadow-heavy);
}

.timeline-content h3 {
  font-size: 1.4rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: var(--text-primary);
}

.timeline-content p {
  font-size: 1.1rem;
  line-height: 1.6;
  opacity: 0.9;
  margin: 0;
  color: var(--text-secondary);
}

/* Team Section */
.team-section {
  padding: 6rem 0;
  margin-bottom: 4rem;
}

.team-section h2 {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 2.5rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 1rem;
  color: var(--text-primary);
}

.section-subtitle {
  font-size: 1.2rem;
  text-align: center;
  margin-bottom: 4rem;
  opacity: 0.8;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  color: var(--text-secondary);
}

.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 3rem;
  max-width: 900px;
  margin: 0 auto;
}

.team-card {
  display: flex;
  gap: 2rem;
  padding: 3rem;
  border-radius: 24px;
  transition: all 0.3s ease;
  background: var(--glass-bg);
  border: 1px solid var(--border-color);
  box-shadow: var(--shadow-light);
}

.team-card:hover {
  transform: translateY(-8px);
  border-color: var(--accent-primary);
  box-shadow: var(--shadow-heavy);
}

.member-avatar {
  width: 80px;
  height: 80px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
  flex-shrink: 0;
  background: var(--gradient-accent);
  box-shadow: 0 4px 20px rgba(139, 92, 246, 0.3);
}

.member-info {
  flex: 1;
}

.member-info h3 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  font-weight: 700;
  color: var(--text-primary);
}

.member-role {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: var(--accent-primary);
}

.member-description {
  font-size: 1.05rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
  opacity: 0.9;
  color: var(--text-secondary);
}

.expertise-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.expertise-tag {
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  background: rgba(139, 92, 246, 0.1);
  color: var(--accent-primary);
  border: 1px solid rgba(139, 92, 246, 0.2);
}

/* Values Section */
.values-section {
  padding: 6rem 0;
  margin-bottom: 4rem;
}

.values-section h2 {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 2.5rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 4rem;
  color: var(--text-primary);
}

.values-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  max-width: 1000px;
  margin: 0 auto;
}

.value-card {
  padding: 3rem 2rem;
  border-radius: 20px;
  text-align: center;
  transition: all 0.3s ease;
  background: var(--glass-bg);
  border: 1px solid var(--border-color);
  box-shadow: var(--shadow-light);
}

.value-card:hover {
  transform: translateY(-8px);
  border-color: var(--accent-primary);
  box-shadow: var(--shadow-heavy);
}

.value-icon {
  font-size: 3rem;
  margin-bottom: 1.5rem;
  display: block;
}

.value-card h3 {
  font-size: 1.4rem;
  margin-bottom: 1rem;
  font-weight: 600;
  color: var(--text-primary);
}

.value-card p {
  font-size: 1.05rem;
  line-height: 1.6;
  opacity: 0.9;
  margin: 0;
  color: var(--text-secondary);
}

/* Technology Section */
.tech-section {
  padding: 6rem 0;
  margin-bottom: 4rem;
}

.tech-section h2 {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 2.5rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 4rem;
  color: var(--text-primary);
}

.tech-stack {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  max-width: 800px;
  margin: 0 auto;
}

.tech-item {
  display: flex;
  align-items: center;
  gap: 2rem;
  padding: 2.5rem;
  border-radius: 20px;
  transition: all 0.3s ease;
  background: var(--glass-bg);
  border: 1px solid var(--border-color);
  box-shadow: var(--shadow-light);
}

.tech-item:hover {
  transform: translateX(10px);
  border-color: var(--accent-primary);
  box-shadow: var(--shadow-heavy);
}

.tech-icon {
  font-size: 3rem;
  flex-shrink: 0;
}

.tech-info h3 {
  font-size: 1.4rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: var(--text-primary);
}

.tech-info p {
  font-size: 1.1rem;
  line-height: 1.6;
  opacity: 0.9;
  margin: 0;
  color: var(--text-secondary);
}

/* CTA Section */
.cta-section {
  padding: 6rem 0;
  text-align: center;
}

.cta-section h2 {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  background: var(--gradient-accent);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.cta-section p {
  font-size: 1.3rem;
  margin-bottom: 3rem;
  opacity: 0.9;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  color: var(--text-secondary);
}

.cta-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

.cta-btn {
  padding: 1.25rem 2.5rem;
  border: none;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 180px;
}

.cta-btn.primary {
  background: var(--gradient-accent);
  color: white;
  box-shadow: 0 4px 20px rgba(139, 92, 246, 0.3);
}

.cta-btn.primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 30px rgba(139, 92, 246, 0.4);
}

.cta-btn.secondary {
  background: transparent;
  border: 2px solid var(--accent-primary);
  color: var(--accent-primary);
}

.cta-btn.secondary:hover {
  transform: translateY(-3px);
  background: rgba(139, 92, 246, 0.1);
}

/* Footer */
.premium-footer {
  margin-top: 6rem;
  padding: 3.8rem 2rem 2rem;
  background: var(--glass-bg);
  border-top: 1px solid var(--border-color);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2.6rem;
  margin-bottom: 2.4rem;
}

.footer-section h4 {
  font-size: 1.2rem;
  margin-bottom: 1rem;
  font-weight: 900;
  background: var(--gradient-accent);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.footer-section p {
  color: var(--text-secondary);
}

.footer-tagline {
  font-style: italic;
  opacity: 0.85;
}

.footer-section a {
  color: var(--text-secondary);
  text-decoration: none;
  margin-top: 0.5rem;
  display: inline-block;
  transition: transform 0.2s ease, color 0.2s ease;
}

.footer-section a:hover {
  color: var(--accent-primary);
  transform: translateX(4px);
}

.footer-bottom {
  text-align: center;
  color: var(--text-secondary);
  border-top: 1px solid var(--border-color);
  padding-top: 1.6rem;
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ===================== RESPONSIVE DESIGN ===================== */
@media (max-width: 1024px) {
  .mission-grid {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
  
  .story-timeline::before {
    left: 30px;
  }
  
  .timeline-item {
    flex-direction: row !important;
    text-align: left;
  }
  
  .timeline-year {
    flex: 0 0 100px;
  }
  
  .nav-brand-text {
    font-size: 1.4rem;
  }
  
  .nav-links {
    gap: 1.8rem;
  }
}

@media (max-width: 768px) {
  .premium-navbar {
    flex-wrap: wrap;
    padding: 1rem;
    gap: 1rem;
  }
  
  .nav-left-section {
    order: 1;
    gap: 1rem;
  }
  
  .nav-brand-text {
    font-size: 1.3rem;
  }
  
  .theme-toggle-btn {
    width: 40px;
    height: 40px;
    font-size: 1.2rem;
  }
  
  .nav-links {
    order: 3;
    width: 100%;
    justify-content: center;
    gap: 1.5rem;
    margin-top: 0.5rem;
  }
  
  .nav-controls {
    order: 2;
    margin-left: auto;
  }

  .about-content {
    padding-top: 7rem;
  }

  .about-hero h1 {
    font-size: 2.5rem;
  }

  .hero-stats {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }

  .team-grid {
    grid-template-columns: 1fr;
  }

  .team-card {
    flex-direction: column;
    text-align: center;
    padding: 2rem;
  }

  .values-grid {
    grid-template-columns: 1fr;
  }

  .tech-item {
    flex-direction: column;
    text-align: center;
    gap: 1rem;
  }

  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }

  .cta-btn {
    width: 100%;
    max-width: 300px;
  }
  
  .footer-content {
    grid-template-columns: 1fr;
    text-align: center;
    gap: 2.5rem;
  }
}

@media (max-width: 480px) {
  .about-content {
    padding-top: 140px;
  }

  .about-hero h1 {
    font-size: 2rem;
  }

  .section-container {
    padding: 0 1rem;
  }

  .mission-content h2,
  .story-section h2,
  .team-section h2,
  .values-section h2,
  .tech-section h2 {
    font-size: 2rem;
  }

  .cta-section h2 {
    font-size: 2.2rem;
  }

  .timeline-content {
    margin: 0 1rem;
    padding: 1.5rem;
  }

  .timeline-year {
    flex: 0 0 80px;
    font-size: 1.2rem;
    padding: 0.75rem;
  }
  
  .nav-left-section {
    gap: 0.6rem;
  }
  
  .nav-logo {
    width: 38px;
    height: 38px;
  }
  
  .nav-brand-text {
    font-size: 1.1rem;
  }
  
  .theme-toggle-btn {
    width: 38px;
    height: 38px;
    font-size: 1.1rem;
  }
  
  .nav-login-btn {
    padding: 0.7rem 1.3rem;
    font-size: 0.9rem;
  }
}