/* src/components/ContactUs.css */

/* ===================== IMPORT THEME TOKENS FROM LOGIN.CSS ===================== */
:root {
  /* Base */
  --bg-primary: #ffffff;
  --bg-secondary: #f7f8fb;
  --bg-tertiary: #eef1f6;

  /* Text */
  --text-primary: #101226;
  --text-secondary: #5a6080;

  /* Brand */
  --accent-primary: #5b7cfa;
  --accent-secondary: #9b5ef7;
  --accent-tertiary: #ff6b6b;

  /* UI */
  --border-color: #e5e9f2;
  --glass-bg: rgba(255, 255, 255, 0.85);
  --glass-border: rgba(255, 255, 255, 0.25);

  /* Shadows */
  --shadow-light: 0 6px 16px rgba(16, 18, 38, 0.06);
  --shadow-heavy: 0 18px 40px rgba(16, 18, 38, 0.14);

  /* Gradients */
  --gradient-light: linear-gradient(135deg, #f9fbff 0%, #eef2f9 45%, #e5eaf6 100%);
  --gradient-accent: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
  --gradient-dark: linear-gradient(135deg, #4966f7, #7a54fa);
}

.dark {
  --bg-primary: #0e1024;
  --bg-secondary: #121632;
  --bg-tertiary: #0a0f27;

  --text-primary: #eef1ff;
  --text-secondary: #9aa2c7;

  --border-color: rgba(255, 255, 255, 0.08);
  --glass-bg: rgba(18, 22, 50, 0.78);
  --glass-border: rgba(255, 255, 255, 0.08);

  --shadow-light: 0 10px 24px rgba(0, 0, 0, 0.28);
  --shadow-heavy: 0 24px 60px rgba(0, 0, 0, 0.4);

  --gradient-light: linear-gradient(135deg, #0e1024 0%, #121632 50%, #0a0f27 100%);
  --gradient-accent: linear-gradient(135deg, #5b7cfa, #9b5ef7);
  --gradient-dark: linear-gradient(135deg, #5b7cfa, #9b5ef7);
}

.contact-us-container {
  min-height: 100vh;
  position: relative;
  padding: 2rem;
  padding-top: 8rem;
}

.contact-content {
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.contact-header {
  text-align: center;
  margin-bottom: 4rem;
  animation: fadeInUp 0.8s ease-out;
}

.contact-header h1 {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 4rem;
  font-weight: 800;
  margin-bottom: 1.5rem;
  background: linear-gradient(135deg, var(--dark-highlight), var(--dark-highlight-hover));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: -2px;
}

body:not(.dark) .contact-header h1 {
  background: linear-gradient(135deg, var(--light-highlight), var(--light-highlight-hover));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.contact-subtitle {
  font-size: 1.3rem;
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto;
  opacity: 0.9;
  font-weight: 500;
}

/* Contact Grid */
.contact-grid {
  display: grid;
  grid-template-columns: 1fr 1.2fr;
  gap: 4rem;
  margin-bottom: 6rem;
}

/* Contact Info Section */
.contact-info-section {
  animation: slideInLeft 0.8s ease-out;
}

.contact-info-section h2 {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

body.dark .contact-info-section h2 {
  color: var(--dark-text-primary);
}

body:not(.dark) .contact-info-section h2 {
  color: var(--light-text-primary);
}

.contact-info-section > p {
  font-size: 1.1rem;
  margin-bottom: 3rem;
  opacity: 0.8;
}

.contact-methods {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.contact-method-card {
  display: flex;
  align-items: flex-start;
  gap: 1.5rem;
  padding: 2rem;
  border-radius: 20px;
  transition: all 0.3s ease;
  cursor: pointer;
}

body.dark .contact-method-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
}

body:not(.dark) .contact-method-card {
  background: rgba(255, 255, 255, 0.6);
  border: 1px solid rgba(0, 0, 0, 0.06);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.04);
}

.contact-method-card:hover {
  transform: translateX(10px);
}

body.dark .contact-method-card:hover {
  border-color: var(--dark-highlight);
  box-shadow: 0 8px 32px rgba(139, 92, 246, 0.2);
}

body:not(.dark) .contact-method-card:hover {
  border-color: var(--light-highlight);
  box-shadow: 0 8px 32px rgba(139, 92, 246, 0.15);
}

.method-icon {
  font-size: 2.5rem;
  flex-shrink: 0;
}

.method-content h3 {
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

body.dark .method-content h3 {
  color: var(--dark-text-primary);
}

body:not(.dark) .method-content h3 {
  color: var(--light-text-primary);
}

.method-details {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
  color: var(--dark-highlight);
}

body:not(.dark) .method-details {
  color: var(--light-highlight);
}

.method-description {
  font-size: 0.95rem;
  opacity: 0.8;
  margin: 0;
}

/* Additional Info */
.additional-info {
  padding: 2rem;
  border-radius: 20px;
  backdrop-filter: blur(20px);
}

body.dark .additional-info {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
}

body:not(.dark) .additional-info {
  background: rgba(255, 255, 255, 0.4);
  border: 1px solid rgba(0, 0, 0, 0.06);
}

.additional-info h3 {
  font-size: 1.3rem;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.additional-info ul {
  list-style: none;
  padding: 0;
}

.additional-info li {
  padding: 0.75rem 0;
  padding-left: 2rem;
  position: relative;
  font-size: 1.05rem;
}

.additional-info li::before {
  content: '';
  position: absolute;
  left: 0;
}

/* Contact Form Section */
.contact-form-section {
  animation: slideInRight 0.8s ease-out;
}

.form-container {
  padding: 3rem;
  border-radius: 24px;
  backdrop-filter: blur(20px);
  height: fit-content;
  position: sticky;
  top: 2rem;
}

body.dark .form-container {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.3);
}

body:not(.dark) .form-container {
  background: rgba(255, 255, 255, 0.8);
  border: 1px solid rgba(0, 0, 0, 0.08);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.1);
}

.form-container h2 {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

body.dark .form-container h2 {
  color: var(--dark-text-primary);
}

body:not(.dark) .form-container h2 {
  color: var(--light-text-primary);
}

.form-container > p {
  font-size: 1.1rem;
  margin-bottom: 2.5rem;
  opacity: 0.8;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.form-group label {
  font-weight: 600;
  font-size: 1rem;
}

body.dark .form-group label {
  color: var(--dark-text-primary);
}

body:not(.dark) .form-group label {
  color: var(--light-text-primary);
}

.form-group input,
.form-group select,
.form-group textarea {
  padding: 1rem 1.25rem;
  border-radius: 12px;
  border: none;
  font-size: 1rem;
  font-family: inherit;
  transition: all 0.3s ease;
  resize: vertical;
}

body.dark .form-group input,
body.dark .form-group select,
body.dark .form-group textarea {
  background: rgba(255, 255, 255, 0.06);
  border: 1.5px solid rgba(255, 255, 255, 0.1);
  color: var(--dark-text-primary);
}

body:not(.dark) .form-group input,
body:not(.dark) .form-group select,
body:not(.dark) .form-group textarea {
  background: rgba(255, 255, 255, 0.9);
  border: 1.5px solid rgba(0, 0, 0, 0.08);
  color: var(--light-text-primary);
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(139, 92, 246, 0.2);
}

body.dark .form-group input:focus,
body.dark .form-group select:focus,
body.dark .form-group textarea:focus {
  border-color: var(--dark-highlight);
}

body:not(.dark) .form-group input:focus,
body:not(.dark) .form-group select:focus,
body:not(.dark) .form-group textarea:focus {
  border-color: var(--light-highlight);
}

.submit-btn {
  padding: 1.25rem 2rem;
  border: none;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  background: linear-gradient(135deg, var(--dark-highlight), var(--dark-highlight-hover));
  color: white;
  box-shadow: 0 4px 20px rgba(139, 92, 246, 0.3);
}

.submit-btn:hover:not(:disabled) {
  transform: translateY(-3px);
  box-shadow: 0 8px 30px rgba(139, 92, 246, 0.4);
}

.submit-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none !important;
}

.loading-spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.success-message {
  padding: 1rem 1.5rem;
  border-radius: 12px;
  background: rgba(16, 185, 129, 0.15);
  border: 1px solid rgba(16, 185, 129, 0.4);
  color: var(--dark-success);
  font-weight: 500;
  text-align: center;
}

/* FAQ Section */
.faq-section {
  margin-bottom: 6rem;
  animation: fadeInUp 0.8s ease-out 0.3s both;
}

.faq-section h2 {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 2.5rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 3rem;
}

.faq-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 2rem;
}

.faq-card {
  padding: 2.5rem;
  border-radius: 20px;
  transition: all 0.3s ease;
}

body.dark .faq-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
}

body:not(.dark) .faq-card {
  background: rgba(255, 255, 255, 0.6);
  border: 1px solid rgba(0, 0, 0, 0.06);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.04);
}

.faq-card:hover {
  transform: translateY(-5px);
}

body.dark .faq-card:hover {
  border-color: var(--dark-highlight);
  box-shadow: 0 8px 32px rgba(139, 92, 246, 0.2);
}

body:not(.dark) .faq-card:hover {
  border-color: var(--light-highlight);
  box-shadow: 0 8px 32px rgba(139, 92, 246, 0.15);
}

.faq-card h3 {
  font-size: 1.3rem;
  margin-bottom: 1rem;
  font-weight: 600;
}

body.dark .faq-card h3 {
  color: var(--dark-text-primary);
}

body:not(.dark) .faq-card h3 {
  color: var(--light-text-primary);
}

.faq-card p {
  font-size: 1.05rem;
  line-height: 1.6;
  margin: 0;
  opacity: 0.9;
}

/* Additional Contact */
.additional-contact {
  text-align: center;
  padding: 4rem;
  border-radius: 24px;
  backdrop-filter: blur(20px);
  margin-bottom: 4rem;
  animation: fadeInUp 0.8s ease-out 0.5s both;
}

body.dark .additional-contact {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
}

body:not(.dark) .additional-contact {
  background: rgba(255, 255, 255, 0.7);
  border: 1px solid rgba(0, 0, 0, 0.06);
}

.additional-contact h2 {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.additional-contact p {
  font-size: 1.2rem;
  margin-bottom: 2.5rem;
  opacity: 0.9;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.cta-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

.cta-btn {
  padding: 1.25rem 2.5rem;
  border: none;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 180px;
}

.cta-btn.primary {
  background: linear-gradient(135deg, var(--dark-highlight), var(--dark-highlight-hover));
  color: white;
  box-shadow: 0 4px 20px rgba(139, 92, 246, 0.3);
}

.cta-btn.primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 30px rgba(139, 92, 246, 0.4);
}

.cta-btn.secondary {
  background: transparent;
  border: 2px solid var(--dark-highlight);
  color: var(--dark-highlight);
}

body:not(.dark) .cta-btn.secondary {
  border-color: var(--light-highlight);
  color: var(--light-highlight);
}

.cta-btn.secondary:hover {
  transform: translateY(-3px);
  background: rgba(139, 92, 246, 0.1);
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Responsive Design */
@media (max-width: 1024px) {
  .contact-grid {
    grid-template-columns: 1fr;
    gap: 3rem;
  }

  .form-container {
    position: static;
  }
}

@media (max-width: 768px) {
  .contact-us-container {
    padding: 1rem;
    padding-top: 7rem;
  }

  .contact-header h1 {
    font-size: 2.5rem;
  }

  .contact-grid {
    gap: 2rem;
  }

  .form-container {
    padding: 2rem;
  }

  .faq-grid {
    grid-template-columns: 1fr;
  }

  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }

  .cta-btn {
    width: 100%;
    max-width: 300px;
  }
}

@media (max-width: 480px) {
  .contact-header h1 {
    font-size: 2rem;
  }

  .form-container h2,
  .faq-section h2,
  .additional-contact h2 {
    font-size: 1.8rem;
  }

  .contact-method-card {
    padding: 1.5rem;
    flex-direction: column;
    text-align: center;
  }

  .method-icon {
    align-self: center;
  }

  .additional-contact {
    padding: 2rem 1.5rem;
  }
}